import React from 'react';
import './footer.css';
import Logo from '../../media/logo.png'; 
import Phone from '../../media/icons/phone.svg';

const Footer = () => {
    return (
        <>
        <div className="footer_section">
            <div className="footer_right--div">
                 <img className="footer_logo" src={Logo} alt="logo" />
                 <div>
                 <h2 className="footer-title" >MKBang Classes</h2>
                 <p className="footer-slogan" >We Can! and We Did! <img className="pen-gif" src="https://media.giphy.com/media/LpDmM2wSt6Hm5fKJVa/giphy.gif" alt="pen"></img></p>
                 </div>
            </div>
            <div className="footer_left--div">
                 <h2 className="footer-contact" >Contact Us</h2>
                 <p className="footer-address" >10/09, Tirupati Apartment, Kagwade Mala, Ichalkaranji, Near Runwal Tower, Layker Galli, Ichalkaranji.</p>
                 <div className="phone-div">
                     <img className="phone-svg" src={Phone} alt="phone"></img>
                     <a href="tel:8275604578" className="contact-num footer-address" >8275604578</a>
                 </div>
                 <div className="phone-div">
                     <img className="phone-svg" src={Phone} alt="phone"></img>
                     <a href="tel:9405842940" className="contact-num footer-address" >9405842940</a>
                 </div>
            </div>
            <div className="policy-container">
                <a className="policy-anchor" target="_blank" href="/privacy-policy">Privacy Policy</a>
                <a className="policy-anchor" target="_blank" href="/terms-of-service">Terms of Service</a>
                <a className="policy-anchor" target="_blank" href="/refund-policy">Refund Policy</a>
            </div>
        </div>
        <div className="dev-credits">
         <p className="credits-p"><a href="https://www.linkedin.com/in/mayank-sonkar-17a8401b6/" target="_blank" className="credits-anchor">Developer</a> &copy; 2021</p>
        </div>
        </>
    )
}
export default Footer