import React,{useState} from 'react';
import './index.css'
import Logo from '../../media/logo.png';
import Home from '../../media/icons/home.png';

const Navbar = () => {
  const [nav,setNav] = useState(false);
    return (
        <div>
         <nav className="navbar">
        <div className="brand-title">
        <img className="logo-img" src={Logo} alt="logo"></img>
        </div>
        {
          !nav?(
            <a className="toggle-button" onClick={() => setNav(true)}>
          <span className="bar"></span>
          <span className="bar"></span>
          <span className="bar"></span>
        </a>
          ): (
            <a className="toggle-button cancel-btn" onClick={() => setNav(false)}>&#10006;</a>
          )
        }
        
        <div className={`${nav?'active':null} navbar-links`}>
          <ul>
            <li className={`${window.location.pathname === '/'?null:"home-anchor"}`}><a  href="/"><img className="home-img" src={Home} alt="home" /></a></li>
            {
              window.location.pathname === '/'?(
                <>
                <li><a href="#about">About</a></li>
            <li><a href="#courses">Course</a></li>
            <li><a href="#faculty">Faculty</a></li>
            <li><a href="#feedback">Feedback</a></li>
            </>
              ):null
            }
          </ul>
        </div>
      </nav>
        </div>
    )
}

export default Navbar;